<template>
  <div class="PersonnelList">
    <v-row>
      <v-col cols="12" md="6">
        <h2 class="subheading grey--text">
          <v-icon class="mx-1">mdi-account</v-icon>
          <span>人員管理</span>
        </h2>
      </v-col>
      <v-col cols="12" md="6">
        <!-- 功能表 -->
        <v-toolbar dense>
          <v-text-field
            hide-details
            prepend-icon="mdi-magnify"
            single-line
            label="搜尋"
            v-model="searchKey"
            v-if="auth.personnel_search_key"
          ></v-text-field>

          <popupadd
            @getAllData="getAllData"
            ref="editProcess"
            :authKeys="authKeys"
            :personnelItems="items"
            v-if="auth.personnel_add_key"
          ></popupadd>
        </v-toolbar>
      </v-col>
    </v-row>

    <!-- <v-progress-linear value="100" class="py-3"></v-progress-linear>  -->
    <v-simple-table fixed-header class="my-8 text-no-wrap" height="77vh">
      <template v-slot:default>
        <thead class="title">
          <tr>
            <th class="text-left"></th>
            <th class="text-left">人員姓名</th>
            <th class="text-left">登入帳號/E-mail</th>
            <th class="text-left">停權</th>
            <th class="text-left">可用權限</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in searchfilter" :key="index">
            <td>
              <v-menu transition="scale-transition" offset-y>
                <template v-slot:activator="{ on }">
                  <v-btn color="primary" dark v-on="on"> 功能 </v-btn>
                </template>
                <v-list>
                  <v-list-item
                    @click="edit(item)"
                    v-if="auth.personnel_edit_key"
                  >
                    <v-list-item-title>修改</v-list-item-title>
                  </v-list-item>
                  <v-list-item @click="del(item)" v-if="auth.personnel_del_key">
                    <v-list-item-title>刪除</v-list-item-title>
                  </v-list-item>
                </v-list>
              </v-menu>
            </td>
            <td>{{ item.username }}</td>
            <td>{{ item.email_address }}</td>
            <td>
              <v-checkbox
                v-model="item.suspension"
                @change="suspensionSet(item)"
              ></v-checkbox>
            </td>
            <td>
              <v-chip
                class="ma-1 white--text"
                color="indigo"
                v-if="item.master_key == 'true'"
                >管理者</v-chip
              >
              <v-chip
                class="ma-1 white--text"
                color="purple"
                v-if="item.option_set_key == 'true'"
                >選單增減</v-chip
              >
              <v-chip
                class="ma-1 white--text"
                color="indigo lighten-2"
                v-if="item.pharmacist_key == 'true'"
                >藥師權限</v-chip
              >

              <div class="d-flex">
                <div v-for="authKey in authKeys" :key="authKey.label">
                  <v-chip
                    :class="authKey.class"
                    :color="authKey.color"
                    v-if="item[`${authKey.keyName}_key`]"
                    >{{ authKey.label }}</v-chip
                  >
                  <v-chip
                    :class="authKey.class"
                    :color="authKey.color"
                    v-if="item[`${authKey.keyName}_add_key`]"
                    >增</v-chip
                  >
                  <v-chip
                    :class="authKey.class"
                    :color="authKey.color"
                    v-if="item[`${authKey.keyName}_edit_key`]"
                    >修</v-chip
                  >
                  <v-chip
                    :class="authKey.class"
                    :color="authKey.color"
                    v-if="item[`${authKey.keyName}_del_key`]"
                    >刪</v-chip
                  >
                  <v-chip
                    :class="authKey.class"
                    :color="authKey.color"
                    v-if="item[`${authKey.keyName}_search_key`]"
                    >查</v-chip
                  >
                </div>
              </div>
            </td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>

    <h2 class="subheading grey--text">
      <v-icon class="mx-1">mdi-tools</v-icon>
      <span>設定管理</span>
    </h2>
    <v-card class="my-3">
      <v-card-text>
        <div class="d-flex justify-space-around">
          <v-checkbox
            label="病歷及病床-隱藏"
            v-model="sethidden"
            @change="changeSet"
          ></v-checkbox>

          <v-checkbox
            label="疫情訪視預約單-不可同時段預約"
            v-model="setvieworder"
            @change="changeSet"
          ></v-checkbox>
        </div>
      </v-card-text>
    </v-card>
    <!-- <pre>{{viewData}}</pre> -->
  </div>
</template>

<script>
import popupadd from "@/views/main/PersonnelAdd";
import dayjs from "dayjs";

export default {
  components: { popupadd },
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      searchKey: "",
      auth: "", //各畫面授權使用功能限定(載入放在mounted)
      sethidden: this.$store.state.cData.sethidden, //病歷及病床顯示
      setvieworder: this.$store.state.cData.setvieworder, //疫情訪視預約單-同時段預約true表示同時段可預約
    };
  },

  computed: {
    authKeys() {
      return this.$store.state.authKeys;
    },
    searchfilter() {
      var keys = this.searchKey.split(" ");
      var str = "";
      return keys.reduce(function (prev, element) {
        return prev.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.items);
    },
  },

  mounted() {
    this.auth = this.$store.state.pData;
    this.getAllData();
  },

  methods: {
    getAllData() {
      //取得所有資料
      this.$api.options(
        "general/getAll/" + this.$store.state.databaseName + "/personnel_nosql"
      ).then((rs) => {
        this.items = rs.map((i) => {
          return {
            ...JSON.parse(i.datalist),
            snkey: i.snkey,
          };
        });
      });
    },
    edit(item) {
      //修改功能
      this.$refs.editProcess.$emit("fromParent", item);
    },
    del(delData) {
      //刪除功能
      this.$confirm("確認刪除?").then((res) => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey: delData.snkey,
          tablename: "personnel_nosql",
          info: JSON.stringify(delData),
        };

        if (res) {
          this.$api.options(
            "general/delv3/" +
              this.$store.state.databaseName +
              "/" +
              postData.tablename,
            postData
          ).then((rs) => {
            if (rs.state == 1) {
              var pop = { msg: "已刪除", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.getAllData();
            }
          });
        }
      });
    },
    suspensionSet(item) {
      item.edit_man =
        this.$store.state.pData.username +
        "(" +
        dayjs().format("YYYY-MM-DD HH:mm:ss") +
        ")" +
        item.edit_man; //執行動作的人,時間,內容

      var postData = {
        snkey: item.snkey,
        datalist: JSON.stringify(item),
        edit_man: "pass",
      };

      this.$api.options(
        "general/edit/" + this.$store.state.databaseName + "/personnel_nosql",
        postData
      ).then((rs) => {
        if (rs.state == 1) {
          var pop = { msg: "停權設定已更改", type: true, theme: "success" };
          this.$store.commit("snackbar", pop);
          this.getAllData();
        }
      });
    },
    //病歷及床位顯示/隱藏設定
    changeSet() {
      let postData = {
        snkey: 1,
        sethidden: this.sethidden,
        setvieworder: this.setvieworder,
        edit_man: "",
      };
      this.$api
        .options(
          "general/edit/" + this.$store.state.databaseName + "/other_data",
          postData
        )
        .then((rs) => {
          if (rs.state == 1) {
            var pop = { msg: "已更改", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);

            //更新暫存
            var newData = this.$store.state.cData;
            newData.sethidden = this.sethidden;
            newData.setvieworder = this.setvieworder;
            this.$store.commit("setcData", newData);
            sessionStorage.setItem("cData", JSON.stringify(newData));
          }
        });
    },
  },
};
</script>


<style lang="scss">
.PersonnelList {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "微軟正黑體";
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>