<template>
  <div class="personneladd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess"
          >mdi-plus-circle</v-icon
        >
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{
          title
        }}</v-card-title>

        <v-card-text>
          <v-form ref="form">
            <v-row>
              <v-col col="12" md="4">
                <v-text-field
                  label="人員姓名"
                  prepend-icon="mdi-account"
                  v-model="list.username"
                  :rules="emptyRules"
                  ref="username"
                ></v-text-field>
              </v-col>
              <v-col col="12" md="4">
                <v-text-field
                  label="登入帳號/E-mail"
                  prepend-icon="mdi-codepen"
                  v-model="list.email_address"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <v-col col="12" md="4">
                <v-text-field
                  label="登入密碼"
                  prepend-icon="mdi-key-variant"
                  v-model="list.password"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <v-col col="12" md="4">
                <v-text-field
                  label="身分證字號"
                  prepend-icon="mdi-id-card"
                  v-model="list.idNum"
                ></v-text-field>
              </v-col>
            </v-row>

            <v-alert outlined color="primary" text>
              <div class="d-flex">
                <header>權限設定</header>
                <v-spacer></v-spacer>
                <v-btn
                  small
                  class="mx-2"
                  color="primary"
                  @click.stop="authSelectAll"
                  >全選</v-btn
                >
                <v-btn small color="info" @click.stop="authCancelAll"
                  >全不選</v-btn
                >
              </div>
              <v-row>
                <v-col
                  cols="12"
                  md="6"
                  v-for="authKey in filterAuthKeys"
                  :key="authKey.keyName"
                >
                  <v-alert
                    :color="authKey.color"
                    text
                    v-if="authKey.keyName != 'exit'"
                  >
                    <v-row justify="space-around" class="px-3">
                      <v-checkbox
                        class="font-weight-black"
                        :label="authKey.label"
                        v-model="list[`${authKey.keyName}_key`]"
                        value="true"
                      ></v-checkbox>
                      <v-row
                        class="ml-10"
                        justify="space-around"
                        v-if="list[`${authKey.keyName}_key`]"
                      >
                        <v-checkbox
                          label="新增"
                          v-model="list[`${authKey.keyName}_add_key`]"
                          value="true"
                          v-if="!['mainboard'].includes(authKey.keyName)"
                        ></v-checkbox>
                        <v-checkbox
                          label="修改"
                          v-model="list[`${authKey.keyName}_edit_key`]"
                          value="true"
                          v-if="!['mainboard'].includes(authKey.keyName)"
                        ></v-checkbox>
                        <v-checkbox
                          label="刪除"
                          v-model="list[`${authKey.keyName}_del_key`]"
                          value="true"
                          v-if="!['mainboard'].includes(authKey.keyName)"
                        ></v-checkbox>
                        <v-checkbox
                          label="查詢"
                          v-model="list[`${authKey.keyName}_search_key`]"
                          value="true"
                          v-if="!['mainboard'].includes(authKey.keyName)"
                        ></v-checkbox>
                      </v-row>
                    </v-row>
                  </v-alert>
                </v-col>

                <v-col cols="12">
                  <v-alert color="warning" text>
                    <h2>特別權限設定</h2>
                    <v-row justify="space-around" class="font-weight-black">
                      <v-checkbox
                        label="管理者"
                        v-model="list.master_key"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        label="選單增減"
                        v-model="list.option_set_key"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        label="藥師權限"
                        v-model="list.pharmacist_key"
                        value="true"
                      ></v-checkbox>
                    </v-row>
                  </v-alert>
                </v-col>
              </v-row>
            </v-alert>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            class="primary"
            @click="addOK"
            v-if="processType == 'add'"
            :loading="loading"
            :disabled="loading"
            >確認新增</v-btn
          >
          <v-btn
            class="success"
            @click="editOK"
            v-if="processType == 'edit'"
            :loading="loading"
            :disabled="loading"
            >確認修改</v-btn
          >
        </v-card-actions>
        <!-- <pre>list:{{ list }}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from 'dayjs'

export default {
  props: ["personnelItems", "authKeys"],
  data() {
    return {
      dialog: false,
      list: { username: "" }, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || "不可空白"],
      loading: false,
    };
  },

  computed: {
    //只保留要顯示的.pass的不列入權限設計
    filterAuthKeys() {
      return this.authKeys.filter((i) => i.authKey != "pass");
    },
    //設定權限設定的初始值,不然全選會沒功能 -> 2021/4/13 直接採用程式的方式去構築,資料來自list中的authKeys
    keyItems() {
      let keyItems = {};
      for (var authKey of this.filterAuthKeys) {
        if (authKey.keyName != "exit") {
          keyItems[`${authKey.keyName}_key`] = "";
          if (!["mainboard"].includes(authKey.keyName)) {
            keyItems[`${authKey.keyName}_add_key`] = "";
          }

          if (!["mainboard"].includes(authKey.keyName)) {
            keyItems[`${authKey.keyName}_edit_key`] = "";
          }

          if (!["mainboard"].includes(authKey.keyName)) {
            keyItems[`${authKey.keyName}_del_key`] = "";
          }

          if (!["mainboard"].includes(authKey.keyName)) {
            keyItems[`${authKey.keyName}_search_key`] = "";
          }
          // keyItems[`${authKey.keyName}_print_key`] = "";
          // if (!["quotation", "costanalysis"].includes(authKey.keyName)) {
          //   keyItems[`${authKey.keyName}_add_key`] = "";
          // }
          // if (["file"].includes(authKey.keyName)) {
          //   keyItems[`${authKey.keyName}_download_key`] = "";
          // }
        }
      }

      //處理額外key
      let special_key = ["master_key", "option_set_key"];
      special_key.forEach((i) => {
        keyItems[i] = "";
      });

      return keyItems;
    },
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function (item) {
      this.dialog = true;
      this.editProcess(item);
    });
  },

  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增人員";
      this.titleStyle = "font-weight-black error lighten-2";
      this.list = { ...this.keyItems };
    },
    editProcess(item) {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改人員資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.list = { ...item }; //複制一份實際修改資料內容
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.list.create_man =
          this.$store.state.pData.username + "(" + dayjs().format("YYYY-MM-DD HH:mm:ss") + ")"; //執行動作的人,時間,內容

        let postData = {
          datalist : JSON.stringify(this.list),
        }   


        this.loading = true;
        this.$api.options(
            "general/add/" + this.$store.state.databaseName + "/personnel_nosql",
            postData
          )
          .then((rs) => {
            if (rs.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.list = {};
              this.dialog = false;
              this.$emit("getAllData");
              this.loading = false;
            }
          });
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        if (!this.list.edit_man){this.list.edit_man = ""}
        this.list["edit_man"] =
          this.$store.state.pData.username + "(" + dayjs().format("YYYY-MM-DD HH:mm:ss") + ")" + this.list.edit_man; //執行動作的人,時間,內容

        let postData = {
          snkey: this.list.snkey,
          datalist : JSON.stringify(this.list),
          edit_man : 'pass'
        } 

        this.$api.options(
            "general/edit/" + this.$store.state.databaseName + "/personnel_nosql",
            postData
          )
          .then((rs) => {
            if (rs.state == 1) {
              var pop = {
                msg: "已修改,權限相關功能將在重新登入後生效",
                type: true,
                theme: "success",
              };
              this.$store.commit("snackbar", pop);
              this.dialog = false;
              this.$emit("getAllData");
              
            }
          });
      }
    },
    authSelectAll() {
      for (var i in this.list) {
        if (i.includes("_key")) {
          this.list[i] = "true";
        }
      }
    },
    authCancelAll() {
      for (var i in this.list) {
        if (i.includes("_key")) {
          this.list[i] = "";
        }
      }
    },
  },
};
</script>

<style lang="scss">
.v-input--selection-controls.v-input .v-label {
  font-size: 1.2rem;
}
</style>